@import url(https://cdnjs.cloudflare.com/ajax/libs/mdb-ui-kit/3.9.0/mdb.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.myMagenta{
  background-color: #8B008B;
}
.navbar {
	margin:0 !important;
	padding: 0px !important;
}

.navbar-brand
{
  font-family: 'Lato', sans-serif;
  margin: 10px;
}

.landing-page-image
{
  background-image: url(/static/media/quiz_time.c336231d.png);
  background-color: black;
  height: 400px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  position: relative;
}

/* main{
    min-height: calc(100vh - 60px- 60px);
} */

.bottom-bar
{
    background-color: #8B008B;
    width:100%;
    height:60px;
    bottom: 0;
    position: fixed; /* instead of absolute */
    bottom: 0;
    left: 0;
}

.message-one
{
    color: #FF97D4;
    text-align: center;
    position: absolute;
    top: 70%;
    left: 20%;
    font-family: Quando;
}

.explore-button
{
    position: absolute;
    top: 70%;
    left: 70%;
    border: none;
    padding: 6px 15px;
    border-radius: 10px;
    color: #FF97D6;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 25px;
    cursor: pointer;
    background-color: #01FDCA;
}

.message-two
{
    color: white;
    text-align: center;
    font-family: Montserrat;
}

.copyright-text
{
    color: white;
    font-size: 15px;
    font-family: Quando;
    position: relative;
    top: 60%;
}

.footer {
    position: fixed; /* instead of absolute */
    bottom: 0;
    left: 0;
    /* The rest of the styles */
  }
.profile-banner
{
    background-color: grey;
    height: 300px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    position: relative;
    border-radius: 0px 0px 80px 80px;
}

.profile-picture
{
    background-color: white;
    height: 200px;
    width: 200px;
    top: 70%;
    margin: auto;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    position: relative;
    border-radius: 100px;
}

.username
{
    color: white;
    font-size: 30px;
    font-family: Montserrat;
    text-align: center;
}

.badges
{
    position: absolute;
    left:3%;
    width: 11%;
}

.badge-label
{
    color: white;
    font: Montserrat;
    font-size: 30px;
}

.badge-num
{
    color: gold;
    font: Montserrat;
    font-size: 30px;
}
